<template>
  <div>
    <div  style="margin-left:50px;">
      <h3>发票信息</h3>
    </div>
    <div>
    	
      <el-form
        class="mt15"
        :status-icon="true"
        :model="form"
        :rules="rules"
        ref="form"
        label-width="120px"
        label-position="right"
      >
        <el-form-item label="发票类型" prop="invoice_type">
          <el-select v-model="form.invoice_type" placeholder="选择发票类型">
            <el-option v-for="(v,i) in invoice_type_list" :label="v.name" :value="v.value" :key="i" :disabled="isShow"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="发票抬头" prop="header">
            <el-col :span="8">
                <el-input v-model="form.header" :disabled="isShow"></el-input>
            </el-col>
        </el-form-item>
       
        <el-form-item label="公司税号" prop="tax_num">
            <el-col :span="8">
                <el-input v-model="form.tax_num" :show-word-limit="true" :disabled="isShow"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="注册地址">
            <el-col :span="8">
                <el-input v-model="form.address"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="注册电话" prop="phone">
            <el-col :span="8">
                <el-input v-model="form.phone" ></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="开户银行" prop="bank">
            <el-col :span="8">
                <el-input v-model="form.bank"></el-input>
            </el-col>
        </el-form-item>
        
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
// import mavonEditor from 'mavon-editor'
// import 'mavon-editor/dist/css/index.css' 
export default {
  name: "App",
  data() {
    return {
      isShow:false,
      invoice_type_list:[
        {
          value:1,
          name:'普通电子发票'
        },
        {
          value:2,
          name:'专用发票'
        }
      ],
      form: {
        invoice_type: "", //发票类型
        header:'', //抬头
        tax_num: "", //税号
        address: "", //地址
        phone: "", //注册手机
        bank: "", //银行名称
      },
      rules: {
        invoice_type: [
          { required: true, message: "请选择发票类型", trigger: "blur" },
        ],
        header: [
          { required: true, message: "请输入发票抬头", trigger: "blur" },
        ],
        tax_num : [
            { required : true, message : '请选择公司税号', trigger : "blur"}
        ]
      }
    };
  },

  created () {

    this.getInfo();
  },
  methods: {
  	getInfo()
  	{
  		this.$api.get('/enterprise/get_enterprise_info',{}, res => {
  			this.form=res.data.data
        this.form.invoice_type=this.form.invoice_type==1 ? '普通电子发票' : '专用发票'
  		});
  	},
    submitForm(formName) {
    	var self = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$api.post('enterprise/edit_enterprise_info', this.form, res=>{
            if(res.data.bol)
            {
              this.$message.success(res.data.msg);
              setTimeout(() => {
               	self.getInfo();
            }, 1500);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style>
body {
  background: #f1f1f1;
}
	.succ , .error, .runing, .wei{color:#fff; padding: 5px 5px 5px 20px; border-radius: 5px;}
  .succ{background:#67C23A;}
  .error{background:#F56C6C}
  .runing{background:#909399}
  .wei{background:#E6A23C;}
</style>



